const checkSubscription = () => {
  const user = getUser();

  if (!user) {
    return false;
  }
  let isIn = user.freetrial
    ? moment(user.freetrial.end).isAfter(moment())
    : false;

  const subend = user.sub;
  let subscribed = false;
  let first = true;
  if (subend && subend.end) {
    subscribed = moment(subend.end * 1000) > moment();
    first = false;
    isIn = false;
  }

  const days = user.freetrial
    ? moment(user.freetrial.end).diff(moment(user.freetrial.end), "days")
    : 0;

  const owner = user.owner;
  const credits = user.freetrial ? user.freetrial.freecredits : 0;
  const bal = user.freetrial
    ? moment(user.freetrial.end).diff(moment(), "days")
    : 0;
  let header = "Welcome to Shadow Properties";
  let body = `You have ${bal} days remaining in your trial with ${parseFloat(
    credits
  ).toFixed(
    2
  )} free credits for mails and smart search. Subscribe now to team plan to add team members and access live driving monitor`;

  let show = false;

  if (isIn) {
    let notifyFreeTrial = getNotifyFreeTrial();
    let notifyTime = moment();

    if (notifyFreeTrial) {
      notifyTime = moment(notifyFreeTrial);
    } else {
      show = true;
    }

    return {
      header,
      body,
      subscribed,
      in: isIn,
      show,
      bal,
      days,
      owner,
    };
  } else {
    header = first ? days + "-Day Trial Expired" : "Subscription Expired";
    body =
      "Thank you for trying Shadow Properties, please subscribe to continue using the app. Feel free to contact us for enquiries.";
    // console.log("subscribed", !subscribed, "first", !first);
    if (!subscribed && !first) {
      body = user.owner
        ? "Please subscribe to continue using the app. Feel free to contact us for enquiries or feature requests. Thank you."
        : "Contact your team owner to renew your subscription and then restart your app. Thank you.";
      show = true;
    } else if (!subscribed) {
      // console.log("SUB", subscribed)
      show = true;
    }
  }

  return {
    header,
    body,
    subscribed,
    in: isIn,
    show,
    bal,
    days,
    owner,
  };
};

const setNotifyFreeTrial = () => {
  localStorage.setItem(
    "@notifyFreeTrail",
    moment().add(1, "day").toISOString()
  );
};

const getNotifyFreeTrial = () => {
  return localStorage.getItem("@notifyFreeTrail");
};

