import React from "react";
import { Center, Image } from "native-base";
import { vw, vh } from "../../../util";
import { CDN, getUser } from "../../../constants/Config";

const UserAvatar = ({ navbar, width, height }) => {
  const user = getUser();
  
  if (!user) {
    return <></>;
  }
  const picid = user.pic || "0";
  const userUri = `${CDN}/users/${user._id}/${picid}`; //+ moment().valueOf();
  // user name
  const fullname = user && user.firstname + " " + user.lastname;
  //avatar fall back
  const nameSplit = fullname.split(" ");
  const fn = nameSplit[0][0];
  const ln = nameSplit[1] ? nameSplit[1][0] : "-";
  const fb = fn + ln;
  const h = vh(height || 68);
  const w = vw(width || 68);
  const ah = vh(height - 8 || 60);
  const aw = vw(width - 8 || 60);

  // console.log(navbar, userUri, height, width);

  return (
    <Center
      // rounded={"full"}
      style={{
        width: w,
        height: h,
        borderWidth: vw(3),
        borderColor: "#E6EBF2",
        backgroundColor: "white",
        borderRadius: 100,
      }}
    >
      <Image
        style={{
          marginX: vw(7),
          marginY: vh(7),
          width: aw,
          height: ah,
        }}
        rounded={"full"}
        alt={fb.toUpperCase()}
        source={{ uri: userUri }}
      />
    </Center>
  );
};

export default UserAvatar;
