import React, { useState, useEffect } from "react";
import { Text, Center, Pressable, VStack } from "native-base";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import moment from "moment-timezone";

import ShadowButton from "../../../components/ShadowButton";
import ShadowModal from "../../../components/ShadowModal";
import BulbIcon from "../../../components/svg/BulbIcon";
import * as campaignActions from "../../../store/actions/campaigns";

const setNotifyGetStarted = () => {
  localStorage.setItem(
    "@notifyGetStarted",
    moment().add(1, "day").toISOString()
  );
};

const getNotifyGetStarted = () => {
  return localStorage.getItem("@notifyGetStarted");
};

const notifyGetStarted = () => {
  const notify = getNotifyGetStarted();
  if (notify) {
    if (moment(notify).isBefore(moment())) {
      return true;
    }
  } else {
    return true;
  }
  return false;
};

const GetStarted = ({ showFreeTrial }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const notify = notifyGetStarted();
  const [isOpen, setIsOpen] = useState(false);
  const [data, setData] = useState(null);

  useEffect(() => {
    (async () => {
      if (showFreeTrial) {
        return;
      }
      const result = await dispatch(campaignActions.getStarted());
      setData(result);
      setIsOpen(notify);
    })();
  }, [notify, showFreeTrial, dispatch]);

  return (
    <VStack
      style={{ height: 24, justifyContent: "center", alignItems: "center" }}
      alignItems={"center"}
      width={24}
    >
      {data && !data.completed && (
        <Pressable
          zIndex={9999}
          style={{}}
          onPress={() => {
            setIsOpen(true);
          }}
        >
          <BulbIcon fill={"rgba(255,0,0,1)"} height={20} width={20} />
        </Pressable>
      )}

      <ShadowModal
        close={() => {
          setNotifyGetStarted();
          setIsOpen(false);
        }}
        size='md'
        isOpen={isOpen && !data.completed}
        header='Get Started'
      >
        <VStack>
          <Center mb={4} h={60} px={4}>
            <Text
              textAlign={"center"}
              fontSize='14'
              color='black'
              _dark={{
                color: "black",
              }}
            >
              Complete the following steps to have your account setup and ready
              to mail property owners
            </Text>
          </Center>
          <Center m={5}>
            <Text
              fontSize={16}
              strikeThrough={data && data.signatures.length > 0}
            >
              Step 1: Create a Signature
            </Text>
            <ShadowButton
              isDisabled={data && data.signatures.length > 0}
              onPress={() => {
                setNotifyGetStarted();
                navigate("/signatures");
              }}
              style={{ width: 100, marginTop: 10 }}
              label='Create'
            />
          </Center>
          <Center m={5}>
            <Text
              fontSize={16}
              strikeThrough={data && data.templates.length > 0}
            >
              Step 2: Create a Template
            </Text>
            <ShadowButton
              isDisabled={
                (data && data.templates.length > 0) ||
                (data && data.signatures.length === 0)
              }
              onPress={() => {
                setNotifyGetStarted();
                navigate("/templates");
              }}
              style={{ width: 100, marginTop: 10 }}
              label='Create'
            />
          </Center>
          <Center m={5}>
            <Text
              fontSize={16}
              strikeThrough={data && data.campaigns.length > 0 ? true : false}
            >
              Step 3: Create a Campaign
            </Text>
            <ShadowButton
              primary
              isDisabled={
                (data && data.campaigns.length > 0) ||
                (data && data.templates.length === 0)
              }
              onPress={() => {
                setNotifyGetStarted();
                navigate("/campaigns");
              }}
              style={{ width: 100, marginTop: 10 }}
              label='Create'
            />
          </Center>
        </VStack>
      </ShadowModal>
    </VStack>
  );
};

export default GetStarted;
